













































import { Component, Vue } from 'vue-property-decorator';
import { Header, SongList, Loading } from '@/components';
import { getHuntingsDetail, getTrackInfo } from '../api';

@Component({
  components: {
    YHeader: Header,
    SongList,
    Loading,
  },
  name: 'detail',
})
export default class HuntingsDetail extends Vue {
  public $refs !: {
    content: Element,
    audio: HTMLAudioElement,
  };

  private created() {
    this.loadData(+this.$route.params.id);
  }

  private data() {
    return {
      data: null,
    };
  }

  get isPlay() {
    return this.$store.state.player.isPlay;
  }

  get currentMusic() {
    return this.$store.state.player.musicList[this.$store.state.player.currentMusicIndex];
  }

  private selectSong(song: any) {
    this.$store.commit('player/add', song);
  }

  private play() {
    this.$store.commit('player/add', (this.data as any).tracks[0]);
    this.$store.commit('player/play');
  }

  private pause() {
    this.$store.commit('player/pause');
  }

  private async loadData(id: number) {
    const res = await getHuntingsDetail(id);
    this.data = res;
  }

  private goBack() {
    this.$router.back();
  }
}
